<template>
	<div>
		<ContentHeader3 title="Administrasi" url="/administrasi" title2="Data Jasa" url2="/data-jasa-pemeriksaan" subTitle="Edit Data Jasa" />
		<div class="content-header">
			<div class="container-fluid">
				<div class="row justify-content-center">
		            <div class="col">
		            	<div class="card">
		            		<div class="card-header row">
		            			<h3 class="card-title">Input Data Administrasi</h3>
		            		</div>
		            		<div class="card-body">
		            			<form @submit.prevent="updateDataAdministrasi">
					                <div class="px-lg-2 ">
					                  <div class="form-group">
					                    <label for="kode">Kode Administrasi <span class="text-danger">*</span></label>
					                    <input type="text" class="form-control" id="kode_administrasi" name="kode_administrasi"  disabled="" v-model="administrasi.kode_administrasi">
					                  </div>
					                  <div class="form-group">
					                    <label for="nama">Nama <span class="text-danger">*</span></label>
					                    <input type="text" class="form-control" id="nama" name="nama" autocomplete="off" placeholder="Nama Administrasi" v-model="administrasi.nama" required="">
					                  </div>
					                  <div class="form-group">
					                    <label for="fee_dokter">Tarif <span class="text-danger">*</span></label>
					                    <div class="input-group mb-3">
						                  <div class="input-group-prepend">
						                    <div type="button" class="btn btn-secondary">Rp.</div>
						                  </div>
						                  <!-- /btn-group -->
					                      <input type="number" class="form-control" id="harga" name="harga" placeholder="Fee Dokter" v-model="administrasi.harga" required="">
						                </div>
					                  </div>
					                  <div class="form-group">
					                    <label for="fee_dokter">Fee Dokter <span class="text-danger">*</span></label>
					                    <div class="input-group mb-3">
						                  <div class="input-group-prepend">
						                    <div type="button" class="btn btn-secondary">Rp.</div>
						                  </div>
						                  <!-- /btn-group -->
					                      <input type="number" class="form-control" id="fee_dokter" name="fee_dokter" placeholder="Fee Dokter" v-model="administrasi.fee_dokter" required="">
						                </div>
					                  </div>
					                  <button type="submit" class="btn btn-primary mt-3">Submit</button>
					                </div>
					                <!-- /.card-body -->					               
					              </form>
		            		</div>
		            	</div>
		            </div>
		        </div>
			</div>
		</div>
	</div>
</template>

<script>
import ContentHeader3 from '@/components/ContentHeader3'

import { ref, onMounted } from 'vue'
import { useRoute } from 'vue-router'
import axios from 'axios'
import router from '@/router'
import Swal from 'sweetalert2'

export default{
	components:{
		ContentHeader3: ContentHeader3
	},
	setup(){
		const route = useRoute()
		const administrasi_id = route.params.id
		const administrasi = ref([])
		
		const getAdministrasiWhereId = async () => {
			let response = await axios.get(`api/administrasi/getWhereId/${administrasi_id}`)
			administrasi.value = response.data
			console.log(administrasi.value)
		}

		const updateDataAdministrasi = () => {
			axios.put(`api/administrasi/update/${administrasi_id}`, administrasi.value)
			.then((response) => {
				Swal.fire({
				  title: 'Berhasil!',
				  text: 'Berhasil mengedit data',
				  icon: 'success',
				  confirmButtonText: 'Ok'
				})	
	    		console.log(response.data)
	    	})
	    	.catch((error) => {
	    		Swal.fire({
				  title: 'Gagal!',
				  text: 'Gagal mengedit data',
				  icon: 'error',
				  confirmButtonText: 'Ok'
				})	
	    		console.log(error)
	    	})	

	    	router.push('/data-jasa-pemeriksaan')
		}

		onMounted(() => {
			getAdministrasiWhereId()
		})

		return {
			administrasi, updateDataAdministrasi
		}
	}
}

</script>


